body {
  color: #111111;
}

.carousel {
  height: 80vh;
}

.carousel-item {
  height: 80vh;
  min-height: 350px;
  background: no-repeat center center scroll;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.carousel-caption h2 {
  font-size: 6vh;
  text-shadow: 2px 2px #111;
}

.carousel-caption p {
  font-size: 4.5vh;
  text-shadow: 2px 2px #111;
}

.navbar {
  background-color: transparent !important;
  transition: 500ms ease;
}

@media screen and (max-width: 767px) {
  .navbar-collapse {
    background-color: #464646;
    padding-top: 5px;
    padding-left: 15px;
    padding-right: 15px;
    padding-bottom: 15px;
    margin-top: -40px;
    z-index: -1;
  }

  .navbar {
    background-color: transparent !important;
    transition: ease 1s;
  }
}

.imgC1 {
  background-image: url("./img/img1.jpg");
  background-size: cover;
}

.imgC2 {
  background-image: url("./img/img2.jpg");
  background-size: cover;
}

.imgC3 {
  background-image: url("./img/img3.jpg");
  background-size: cover;
}

.embed {
  width: 1000px;
  height: 500px;
}

.imgAdm {
  width: 150px;
  height: 150px;
}

.color:nth-child(even) {
  background-color: #f1f1f1;
}

.img-team {
  width: 550px;
  height: 400px;
}

.card {
  width: 18rem;
}

.scrolled {
  background-color: #3d3d3d !important;
}

.unscrolled {
  background-color: transparent;
}

.hide {
  animation-name: hide;
  animation-duration: 1s;
}

@keyframes hide {
  0% {
    opacity: 100%;
  }
  50% {
    opacity: 50%;
  }
  99% {
    opacity: 0%;
  }
  100% {
    position: absolute;
  }
}

.abs {
  position: absolute;
}

.socials i {
  color: #ffffff;
}

.navbar-brand i {
  opacity: 0%;
}
